    };
  },
  computed: {
    componentsListComputed() {
      const self = this;
      const list = self.componentsList.sort().map((c) => {
        const name = c
          .split("-")
          .map((word) => {
            return word[0].toUpperCase() + word.substring(1);
          })
          .join(" ");
        return {
          component: c,
          name,
        };
      });
      return list;
    },
  },
  watch: {
    "theming.fillBars": function (fillBars) {
      const self = this;
      if (!self.barsStyleEl) {
        self.barsStyleEl = document.createElement("style");
        self.barsStyleEl.innerHTML = `
            /* Invert navigation bars to fill style */
            :root,
            :root.theme-dark,
            :root .theme-dark {
              --zmp-bars-bg-color: var(--zmp-theme-color);
              --zmp-bars-text-color: #fff;
              --zmp-bars-link-color: #fff;
              --zmp-navbar-subtitle-text-color: rgba(255,255,255,0.85);
              --zmp-bars-border-color: transparent;
              --zmp-tabbar-link-active-color: #fff;
              --zmp-tabbar-link-inactive-color: rgba(255,255,255,0.54);
              --zmp-searchbar-bg-color: var(--zmp-bars-bg-color);
              --zmp-searchbar-input-bg-color: #fff;
              --zmp-searchbar-input-text-color: #000;
              --zmp-sheet-border-color: transparent;
              --zmp-tabbar-link-active-border-color: #fff;
            }
            .appbar,
            .navbar,
            .toolbar,
            .subnavbar,
            .calendar-header,
            .calendar-footer {
              --zmp-touch-ripple-color: var(--zmp-touch-ripple-white);
              --zmp-link-highlight-color: var(--zmp-link-highlight-white);
              --zmp-button-text-color: #fff;
              --zmp-button-pressed-bg-color: rgba(255,255,255,0.1);
            }
          `;
      }
      if (fillBars) {
        zmp.$("head").append(self.barsStyleEl);
      } else {
        zmp.$(self.barsStyleEl).remove();
      }
    },
    "theming.darkTheme": function (darkTheme) {
      const self = this;
      const html = zmp.$("html");
      if (darkTheme) html.addClass("theme-dark");
      else html.removeClass("theme-dark");
    },
    "theming.customColor": function (customColor) {
      const self = this;
      const color = self.theming.color;
      const cssVars = zmp.utils.colorThemeCSSProperties(color);
      const html = zmp.$("html")[0];
      if (customColor) {
        Object.keys(cssVars).forEach((key) => {
          html.style.setProperty(key, cssVars[key]);
        });
      } else {
        Object.keys(cssVars).forEach((key) => {
          html.style.removeProperty(key);
        });
      }
    },
    "theming.color": function () {
      const self = this;
      const color = self.theming.color;
      const cssVars = zmp.utils.colorThemeCSSProperties(color);
      const html = zmp.$("html")[0];
      Object.keys(cssVars).forEach((key) => {
        html.style.setProperty(key, cssVars[key]);
      });
    },
    customBuild() {
      const self = this;
      self.bundler = "webpack";
      self.cssPreProcessor = "less";
    },
    log() {
      const self = this;
      self.$nextTick(() => {
        const { logEl } = self.$refs;
        logEl.scrollTop = logEl.scrollHeight - logEl.offsetHeight;
      });
    },
    done() {
      const self = this;
      if (self.done && self.iconFile) {
        self.done = false;
        self.loading = true;
        self.iconFile = null;
        self.generatingAssets = true;
        request
          .postJSON("/api/assets/generate/", { keepLog: true })
          .then(() => {
            self.getLog();
          });
      }
    },
  },
  mounted() {
    const self = this;
    request.json("/api/cwd/").then((res) => {
      self.cwd = res.data.cwd;
    });
  },
  methods: {
    logText,
    exportSettings() {
      const self = this;
      const options = self.getOptions();
      delete options.cwd;

      const data = [JSON.stringify(options, "", 2)];

      let file;
      const fileName = `${options.name || "zmp"}.json`;
      const properties = { type: "application/json" };
      try {
        file = new File(data, fileName, properties);
      } catch (e) {
        file = new Blob(data, properties);
      }
      const url = URL.createObjectURL(file);
      const link = document.createElement("a");
      link.download = fileName;
      link.target = "_blank";
      link.href = url;
      link.click();
    },
    importSettings(data) {
      const self = this;

      const {
        bundler,
        cssPreProcessor,
        customBuild,
        customBuildConfig,
        framework,
        name,
        pkg,
        template,
        theming,
        type,
        webpack,
      } = data;

      if (bundler === "webpack" && webpack) {
        self.bundlerAdvanced = true;
      }
      Object.assign(self, {
        bundler,
        cssPreProcessor,
        customBuild,
        customBuildConfig,
        framework,
        name,
        pkg,
        template,
        theming,
        type,
        webpack,
      });
    },
    onImportInputChange(e) {
      const self = this;
      const reader = new FileReader();
      reader.onload = () => {
        const content = JSON.parse(reader.result);
        self.importSettings(content.zmp || content);
      };
      reader.readAsText(e.target.files[0]);
    },
    resetIcon() {
      this.iconFile = null;
      this.iconPreview = null;
      this.$refs.iconInput.value = null;
    },
    onIconChange(e) {
      const file = e.target.files[0];
      if (!file) return;
      const reader = new FileReader();
      reader.onload = () => {
        this.iconPreview = reader.result;
      };
      this.iconFile = file;
      reader.readAsDataURL(file);
    },
    toggleComponents() {
      const self = this;
      if (
        self.customBuildConfig.components.length === self.componentsList.length
      ) {
        self.customBuildConfig.components = [];
      } else {
        self.customBuildConfig.components = [...self.componentsList];
      }
    },
    toggleArrayValue(arr, value, remove) {
      if (remove) {
        if (arr.indexOf(value) >= 0) {
          arr.splice(arr.indexOf(value), 1);
        }
        return;
      }
      if (arr.indexOf(value) < 0) {
        arr.push(value);
      } else {
        arr.splice(arr.indexOf(value), 1);
      }
    },

    getOptions() {
      const self = this;
      const {
        cwd,
        name,
        appId,
        type,
        pkg,
        framework,
        template,
        bundler,
        cssPreProcessor,
        webpack,
        theming,
        customBuild,
        customBuildConfig,
      } = self;
      const options = {
        cwd,
        type,
        name,
        appId,
        framework,
        template,
        bundler,
        cssPreProcessor,
        theming,
        customBuild,
      };
      if (options.customBuild) {
        options.bundler = "webpack";
        options.cssPreProcessor = "less";
        options.customBuildConfig = customBuildConfig;
      }
      if (options.bundler !== "webpack") {
        options.cssPreProcessor = false;
      }
      if (options.bundler === "webpack") {
        options.webpack = webpack;
      }

      return options;
    },
    showError(message) {
      const self = this;
      zmp.dialog.alert(message);
    },
    getLog() {
      const self = this;
      getLog(
        self,
        self.generatingAssets ? "/api/assets/generate/" : "/api/create/"
      );
    },
    createApp() {
      const self = this;
      if (self.loading) return;
      const options = self.getOptions();
      if (!options.type.length) {
        self.showError(
          "You must specify app type (Web app, PWA)"
        );
        return;
      }
      if (!options.name) {
        self.showError("You must specify app name");
        return;
      }
      if (!options.framework) {
        self.showError("You must specify a framework");
        return;
      }
      self.loading = true;
      const data = new FormData();
      data.set("iconFile", this.iconFile);
      data.set("options", JSON.stringify(options));
      request.post("/api/create/", data).then(() => {
        self.getLog();
      });
    },
  },
};
</script>
