// Import Vue
import { createApp } from 'vue';

// Import ZMP
import ZMP from 'zmp-core/lite';
import Input from 'zmp-core/components/input';
import Dialog from 'zmp-core/components/dialog';
import ColorPicker from 'zmp-core/components/color-picker';
import Popover from 'zmp-core/components/popover';
import Range from 'zmp-core/components/range';
import Toggle from 'zmp-core/components/toggle';
import Popup from 'zmp-core/components/popup';
import Tooltip from 'zmp-core/components/tooltip';

// Import ZMP-Vue Plugin
import ZMPVue, { registerComponents } from 'zmp-vue/bundle';

// Import ZMP Styles
import 'zmp-core/zmp-bundle.css';

// Import Icons and App Custom Styles
import '../css/icons.css';
import '../css/app.less';

// Import App Component
import App from '../components/app.vue';

// Init ZMP-Vue Plugin
ZMP.use(ZMPVue);
ZMP.use([
  Input,
  Dialog,
  ColorPicker,
  Popover,
  Range,
  Toggle,
  Popup,
  Tooltip,
]);

const app = createApp(App);

registerComponents(app);

app.mount('#app');

export default app;
