<template>
  <zmp-page name="deploy">
    <zmp-navbar :sliding="false" large>
      <zmp-nav-title>
        <i class="zmp-navbar-logo"></i>
        <span>Deploy App</span>
      </zmp-nav-title>

      <zmp-nav-title-large>
        <i class="zmp-navbar-logo"></i>
        <span>Deploy App</span>
      </zmp-nav-title-large>
    </zmp-navbar>

    <div class="center-content">
      <zmp-block medium-inset strong class="content-block">
        <zmp-block-title large>
          <i class="zmp-icons block-icon">info_circle</i>
          <span>General</span>
        </zmp-block-title>
        <zmp-block-title
          >What type of the server are you deploying?</zmp-block-title
        >
        <div class="row checkbox-row">
          <div
            class="col-100 medium-50 checkbox-col"
            :class="{ checked: server === 'prod' }"
          >
            <div
              class="col-icon"
              @click="
                server = 'prod';
              "
            >
              <span class="text-icon">Production</span>
            </div>
          </div>
          <div
            class="col-100 medium-50 checkbox-col"
            :class="{ checked: server === 'dev' }"
          >
            <div
              class="col-icon"
              @click="
                server = 'dev';
              "
            >
              <span class="text-icon">Development</span>
            </div>
          </div>
        </div>
        <zmp-block-title medium>Description</zmp-block-title>
        <zmp-list no-hairlines-between class="inputs-list">
          <zmp-list-input
            type="text"
            outline
            clear-button
            required
            validate
            validate-on-blur
            :value="desc"
            @input="desc = $event.target.value"
          ></zmp-list-input>
        </zmp-list>
      </zmp-block>

      <zmp-popup
        class="popup-log"
        :closeByBackdropClick="false"
        :opened="log && log.length > 0"
      >
        <pre ref="logEl" v-html="logText(log)"></pre>
      </zmp-popup>

      <zmp-block medium-inset class="no-padding button-block">
        <zmp-button
          v-if="!done && !error"
          :class="{ loading: loading }"
          class="button-center-content"
          style="width: 300px"
          large
          fill
          round
          @click="deployApp"
          icon-zmp="gear_alt_fill"
          :text="loading ? 'Deploying app...' : 'Deploy App'"
        ></zmp-button>
        <zmp-button
          v-if="done"
          class="button-center-content"
          style="width: 300px"
          large
          fill
          round
          icon-zmp="checkmark_alt"
          text="Done"
          color="green"
        ></zmp-button>
        <zmp-button
          v-if="error"
          class="button-center-content"
          style="width: 300px"
          large
          fill
          round
          icon-zmp="xmark"
          text="Error"
          color="red"
        ></zmp-button>
      </zmp-block>
    </div>
  </zmp-page>
</template>
<script>
import { zmp } from "zmp-vue";
import { request } from "zmp-core";
import logText from "../utils/log-text";
import getLog from "../utils/get-log";

export default {
  data() {
    return {
      loading: false,
      log: [],
      done: false,
      error: false,
      cwd: "",
      desc: "Update UI",
      server: "prod"
    };
  },
  mounted() {
    const self = this;
    request.json("/api/cwd/").then((res) => {
      self.cwd = res.data.cwd;
    });
  },
  methods: {
    logText,
    showError(message) {
      const self = this;
      zmp.dialog.alert(message);
    },
    getLog() {
      const self = this;
      getLog(
        self,
        self.generatingAssets ? "/api/assets/generate/" : "/api/deploy/"
      );
    },
    getOptions() {
      const self = this;
      const {
        cwd,
        server,
        desc
      } = self;
      const options = {
        cwd,
        server,
        desc
      };

      return options;
    },
    deployApp() {
      const self = this;
      if (self.loading) return;
      const options = self.getOptions();
      self.loading = true;
      const data = new FormData();
      data.set("options", JSON.stringify(options));
      request.post("/api/deploy/", data).then(() => {
        self.getLog();
      });
    },
  },
};
</script>
